class AbstractView {
  constructor(params = null) {
    this.setupParameters(params);
    this.construct();
    this.setDefaultRender();
  }

  setupParameters() {
    //  
    this.started = false;
    this.playing = false;
    this.resizeInProgress = false;

    //  
    //   
    this.width = window.innerWidth;
    this.height = window.innerHeight;

    //      
    this.renderFunc = null;
  }

  construct() {
    console.warn(` AbstractView  . 
       construct     .`);
  }

  setDefaultRender() {
    this.renderFunc = () => {
      this.renderer.render(this.scene, this.camera);
    };
  }

  setRenderFunction(func) {
    if (typeof func !== `function`) return;
    this.renderFunc = func;
  }

  render() {
    if (this.renderFunc !== null) {
      this.renderFunc.call(this);
    }
  }

  start() {
    if (this.started) return this;

    this.started = true;

    //   ,    
    this.startResize();
    
    //   
    this.play();

    return this;
  }

  stop() {
    if (!this.started)  return this;

    this.pause();
    this.stopResize();
    this.started = false;

    return this;
  }

  startResize() {
    //       , 
    //     ,       .
    //         resize
    this.onResize = () => {
      if (this.resizeInProgress) {
        return;
      }
      this.resizeInProgress = true;
    };

    //   ,    
    //   
    this.resize();

    window.addEventListener(`resize`, this.onResize);
  }

  stopResize() {
    if (!this.onResize) return;

    window.removeEventListener(`resize`, this.onResize);
    this.onResize = null;

    //     
    this.resizeInProgress = false;
  }

  play() {
    if (this.playing) return this;

    const onAnimationFrame = (target) => {
      //      ,  
      if (target.resizeInProgress) target.resize();
      // 
      target.render();
  
      //  ,        
      target.resizeInProgress = false;
  
      // ,     requestAnimationFrame
      if (!target.started || !target.playing) return;

      //   
      requestAnimationFrame(() => {
        onAnimationFrame(target);
      });
    };

    //   ,    
    //  requestAnimationFrame
    //    resizeInProgress
    this.playing = true;

    //      ,    , 
    //    
    this.render();
    
    //     
    requestAnimationFrame(() => {
      onAnimationFrame(this);
    });

    return this;
  }

  pause() {
    if (!this.playing) return this;

    //   - ,      
    this.playing = false;

    return this;
  }

  resize() {
    console.warn(` AbstractView  . 
       resize     .`);
  }
}

export default AbstractView;
